<template>
  <view class="container">
    <SafeTopContainer fixed>
      <!-- ======================= 标题栏 ======================= -->
      <view class="nav-bar">
        <view class="nav-left" @click="handleClickLeft">
          <wd-icon name="thin-arrow-left" color="#fff" size="22px"></wd-icon>
        </view>
        <view class="nav-title">
          {{ isOffer ? "My Offerings" : "My Requests" }}
        </view>
      </view>
    </SafeTopContainer>
    <!-- ======================= 内容区域======================= -->
    <view class="the_detail">
      <!-- ======================= 小卡片 ======================= -->
      <view class="item-card">
        <view class="item-icon">
          <image
            :src="dataSource.cate_image"
            mode="widthFix"
            style="width: 100%"
          />
        </view>
        <view class="item-content">
          <view class="item-title">
            {{ dataSource.cate_name }}
          </view>
          <view class="item-desc" v-if="dataSource.title">
            <view class="desc_icon">
              <image
                :src="getIcon(dataSource)"
                mode="widthFix"
                style="width: 100%"
              />
            </view>
            <view class="right-box">
              <text v-if="dataSource.method == 2 || dataSource.method == 4">
                {{ dataSource.title }}
              </text>
              <text v-if="dataSource.method == 1 || dataSource.method == 3">
                {{ dataSource.from }} -> {{ dataSource.to }}
              </text>
            </view>
          </view>
          <view class="item-price">
            <view class="price_icon">
              <image
                src="@/static/price-icon.svg"
                mode="widthFix"
                style="width: 100%"
              />
            </view>
            ${{ dataSource.price }}
          </view>
        </view>
      </view>
      <!--匹配等级-->
      <view class="resource-match-level-wrapper">
        <text>Resources Match level Powered by AI</text>
        <view class="flex items-center power-bar-list">
          <view class="power-bar" v-for="i in 4" :key="i"> </view>
          <view class="power-bar non-select"> </view>
          <text class="powner-text">75%</text>
        </view>
      </view>
      <!-- ======================= 详情内容 ======================= -->
      <view class="item-detail">
        <text class="id_title">{{ dataSource.title }}</text>
        <view class="id_info">
          <template v-if="isService">
            <text>
              <text style="font-weight: 800">From:</text>
              {{ dataSource.from }}
            </text>
            <text>
              <text style="font-weight: 800">To:</text>
              {{ dataSource.to }}
            </text>
          </template>

          <text v-if="dataSource.datetime && dataSource.datetime.length > 0">
            <text style="font-weight: 800">Date:</text>
            {{ dataSource.datetime }}
          </text>
          <text>
            <text style="font-weight: 800">Description:</text>
            {{ dataSource.description }}
          </text>
        </view>
        <view class="bottom-confirm-container flex items-center">
          <image
            v-for="image in dataSource.images"
            :src="image"
            mode="widthFix"
            style="width: 520rpx; height: 520rpx"
          />
        </view>
      </view>
    </view>
  </view>

  <view class="confirm-button-wrapper">
    <view
      class="confirm-button"
      v-if="isMatchMode"
      @click="handleClickConfirmMatch"
    >
      CONFIRM TO MATCH
    </view>
  </view>

  <CustomTabbar />
</template>

<script setup>
import img_request_locate from "@/static/explore/general/request_locate.png"
import img_request_checked from "@/static/explore/general/request_checked.png"
import img_offering_locate from "@/static/explore/general/offering_locate.png"
import img_offering_checked from "@/static/explore/general/offering_checked.png"

import { indexOrderDetail } from "@/api/totalApi"
import SafeTopContainer from "@/components/SafeTopContainer.vue"
import { useNavigate } from "@/hooks/useNavigate"
import CustomTabbar from "@/components/CustomTabbar.vue"

const dataSource = ref({})
const isMatchMode = ref(false)

const isGoods = computed(
  () => dataSource.value.method == 2 || dataSource.value.method == 4,
)
const isService = computed(
  () => dataSource.value.method == 1 || dataSource.value.method == 3,
)
const isRequest = computed(() => dataSource.value.methodtype == 2)
const isOffer = computed(() => dataSource.value.methodtype == 1)

const handleClickLeft = () => {
  uni.navigateBack()
}

const getIcon = (item) => {
  // 提供
  if (item.methodtype == 2) {
    if (item.method == 1 || item.method == 3) {
      return img_request_locate
    } else {
      return img_request_checked
    }
  }
  // 需求
  else if (item.methodtype == 1) {
    if (item.method == 2 || item.method == 4) {
      return img_offering_checked
    } else {
      return img_offering_locate
    }
  }
}

const handleClickConfirmMatch = async () => {
  await useNavigate("/pages/tracking/confirm", { orderId: dataSource.value.id })
}

onLoad(async (option) => {
  if (option.id) {
    const r = await indexOrderDetail({ id: option.id })
    dataSource.value = r.data
  }

  if (option.match) {
    isMatchMode.value = true
  }
})
</script>
